* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'montserrat', sans-serif;
  background-color: #28262c;
}

.App-header {
  padding: 50px 15px 0;
}

.App-header h1 {
  color: #ffffff;
  font-size: 48px;
  font-weight: 500;
  text-align: center;
}

main {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

section {
  padding: 15px;
}

.searchBox {
  display: block;
  width: 100%;
  padding: 15px;

  border: none;
  outline: none;
  background: none;

  background-color: #eeeeee;
  border-radius: 8px;

  color: #53565a;
  font-size: 20px;
  font-weight: 300;

  transition: 0.4s ease-out;
}

.searchBox:focus{
  box-shadow: 0px 0px 8px 3px #222222;
}

.results {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px;
}

.results .result {
  width: 25%;
  max-width: 250px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.results .result img {
  display: block;
  width: 100%;
}

.results .result h3 {
  color: #ffffff;
  font-size: 20px;
  font-weight: 600;
  padding: 10px;
  background: #444444;
  flex: 1 1 100%;
  transition: 0.4s ease-out;
}

.results .result:hover h3 {
  background: #ffffff;
  color: #222222;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #444444;
  overflow-y: scroll;
}

.popup .content {
  display: block;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;

  padding: 25px;
  color: #ffffff;
}

.popup .content h2 {
  font-size: 28px;
  font-weight: 600;
}

.popup .content h2 span {
  font-size: 20px;
  font-weight: 300;
}

.popup .content .rating {
  margin-bottom: 30px;
}

.popup .content .plot {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px -30px;
}

.popup .content .plot img {
  flex: 1 1 50%;
  max-width: 300px;
  padding: 0px 15px;
}

.popup .content .plot p {
  flex: 1 1 50%;
  padding: 15px;
}

.popup .content .close {
  display: inline-block;
  padding: 10px 30px;
  font-size: 18px;
  margin: 50px 0 0 0;
  font-weight: 700;

  background: #d93411;
  color: #FCF6F4;
  border-radius: 8px;
  border: none;
  outline: none;
  appearance: none;

  cursor: pointer;
  transition: 0.4s ease-out;
}

.popup .content .close:hover {
  background: #7A1709;
}


/*Media queries for tablet and mobile devices*/
@media only screen and (max-width: 768px) {
  .results .result {
    width: 33.333%;
    max-width: 250px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
  }
}

@media only screen and (max-width: 600px) {
  .results .result {
    width: 50%;
    max-width: 250px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
  }
}
